body {
    margin: 0px;
    font-size: 14px;
    background: #1c1c1c;
    color: white;
}

.blur-bg {
    -webkit-transition: background-image 1s ease-in-out;
    -webkit-filter: blur(75px) brightness(0.35);
    height: 100vh;
    width: 100vw;

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;

    position: fixed;
    top: 0;
    left: 0;
}

.loading-gif {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100vw;
}
.loading-gif img {
    width: 60px;
}
.main {
    background: #1c1c1c;
    font-family: sans-serif;
}

/* ScrollBar */
::-webkit-scrollbar {
    width: 5px;
    cursor: pointer;
}

::-webkit-scrollbar-track {
    background: #1c1c1c;
}

::-webkit-scrollbar-thumb {
    background: #49ba8e;
}
